//
// Label
//

.label {
    @include label;
}

@each $color, $value in $colors {
    .label.label-#{$color} {
        color: $white;
        border: $border-width solid #{$value};
        background-color: #{$value};
    }
}

@each $color, $value in $theme-colors {
    .label.label-#{$color} {
        color: $white;
        border: $border-width solid #{$value};
        background-color: #{$value};
    }
}

@each $color, $value in $status-colors {
    .label.label-#{$color} {
        color: $white;
        border: $border-width solid #{$value};
        background-color: #{$value};
    }
}

@each $color, $value in $social-colors {
    .label.label-#{$color} {
        color: $white;
        border: $border-width solid #{$value};
        background-color: #{$value};
    }
}
