//
// Button
//

.button {
    @include button;
}

// Button sizes
.button {
    @include button-size;
}

@include button-sizes;

// Button colors
@each $color, $value in $colors {
    .button-#{$color} {
        @include button-color($white, $value);
    }
}

@each $color, $value in $theme-colors {
    .button-#{$color} {
        @include button-color($white, $value);
    }
}

@each $color, $value in $status-colors {
    .button-#{$color} {
        @include button-color($white, $value);
    }
}

@each $color, $value in $social-colors {
    .button-#{$color} {
        @include button-color($white, $value);
    }
}

// Group
.button-group {
    overflow: hidden;
    margin-left: -1px;
    margin-right: -1px;
    padding: 0 1px;
    box-shadow: $box-shadow;

    @include support-flexbox {
        display: flex;
        align-items: stretch !important;
        justify-content: space-between;
        border-radius: $border-radius;

        > .button {
            display: flex;
            flex-direction: column;
            flex-grow: 1;
            align-items: center;
            margin-left: -1px;
            border-radius: 0 !important;

            &:first-child {
                margin-left: 0;
                border-top-left-radius: $border-radius !important;
                border-bottom-left-radius: $border-radius !important;
            }

            &:last-child {
                border-top-right-radius: $border-radius !important;
                border-bottom-right-radius: $border-radius !important;
            }
        }
    }
    @include not-support-flexbox {
        > .button {
            float: left;
            margin: 3px;
        }
    }
}

.share-button {
    display: inline-block;
    color: $white !important;
    text-decoration: none !important;
    padding: .375em .5em;
    border-radius: $border-radius;
    border: 2px solid $border-color;
    line-height: 1;
    text-align: center;

    [class*="icon"] {
        margin-left: 0;
        margin-right: .250em;
    }
    @include media-breakpoint-down(dt) {
        padding: .750em .750em;
        [class*="icon"] {
            font-size: 2em;
            margin: 0 0 .250em;
            display: block;
        }
    }
    @include media-breakpoint-down(sp) {
        [class*="icon"] {
            font-size: 3em;
        }
        span.hide-text {
            display: none;
        }
    }
}

.share-button-facebook {
    background: map-get($social-colors, "facebook");
    border-color: map-get($social-colors, "facebook");
