//
// Button
//

@mixin button {
    outline: $border-width solid transparent;
    display: inline-block;
    overflow: hidden;
    vertical-align: top;
    text-align: center;
    color: $base-color !important;
    background: $white;
    border-width: $border-width;
    border-style: solid;
    border-color: $border-color;
    border-radius: $border-radius !important;
    box-shadow: $box-shadow, $box-shadow-up inset;
    cursor: pointer;
    appearance: none;
    text-decoration: none;

    &.active,
    &:active {
        transform: translateY(1px);
        border-color: $border-color;
        box-shadow: inset $box-shadow, inset 0 0 10em rgba(0, 0, 0, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.1);
    }

    @include ellipsis;
    @include hover {
        text-decoration: none;
    }
}

@mixin button-size($size: $button-default-size) {
    padding: $size;
}

@mixin button-sizes($device: "") {
    $device: class-name($device);

    @each $key, $size in $button-sizes {
        $name: class-name($key);

        &.#{$device}#{$name}button {
            @include button-size($size);
        }

        .button-group.#{$device}#{$name}button {
            .button {
                @include button-size($size);
            }
        }
    }
}

@mixin button-color($text-color, $button-color) {
    color: $text-color !important;
    background: $button-color;
    border-color: darken($button-color, 5%) darken($button-color, 10%) darken($button-color, 10%);

    &.active,
    &:active {
        background: darken($button-color, 3%) !important;
        border-color: darken($button-color, 10%) darken($button-color, 10%) darken($button-color, 5%) !important;
    }
}
