//
// List
//

@mixin list-size($size: $list-default-size) {
    padding-left: 0;
    list-style: none;

    > li {
        margin-top: $size;

        &:first-child {
            margin-top: 0;
        }
    }

    &.-horizontal {
        > li {
            margin-top: 0;
            margin-right: $size;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    &.list-border {
        padding-bottom: $size;
        border-bottom: $border-width solid $border-color;

        > li {
            margin-top: $size;
            padding-top: $size;

            &:first-child {
                margin-top: 0;
            }
        }

        &.-unplug {
            padding-bottom: 0;

            > li {
                &:first-child {
                    padding-top: 0;
                }
            }
        }

        &.-horizontal {
            padding-bottom: 0;

            > li {
                margin: 0 0 0 $size;
                padding: 0 0 0 $size;

                &:first-child {
                    margin-left: 0;
                }

                &:last-child {
                    margin-right: 0;
                    padding-right: $size;
                }
            }

            &.-unplug {
                > li {
                    margin-top: 0;
                    padding-top: 0;

                    &:first-child {
                        margin-left: 0;
                        padding-left: 0;
                    }

                    &:last-child {
                        padding-right: 0;
                    }
                }
            }
        }
    }
}

@mixin list-sizes($device: "") {
    $device: class-name($device);

    @each $key, $size in $list-sizes {
        $name: class-name($key);

        .#{$device}#{$name}list {
            @include list-size($size);
        }
    }
}
