//
// Table
//

table {
    th, td {
        font-weight: $table-th-weight;
        vertical-align: top;
        text-align: left;
        word-wrap: break-word;
    }

    &.layout-fixed {
        table-layout: fixed;
    }
}

table.table {
    border-collapse: separate;
    border-spacing: 0;
    border-bottom: $border-width solid $border-color;

    > thead {
        th {
            border-bottom: $border-width solid $border-color;
        }
    }

    th, td {
        border-top: $border-width solid $border-color;
    }

    table {
        th, td {
            border-top: none;
        }
    }

    &.-fill {
        th,
        td:first-child {
            padding-left: 0;
        }

        td:last-child {
            padding-right: 0;
        }
    }
}

table.table.table-bordered {
    border-right: $border-width solid $border-color;
    border-bottom: $border-width solid $border-color;

    th, td {
        border-top: $border-width solid $border-color;
        border-left: $border-width solid $border-color;
    }
}

table.table.table-striped {
    tr:nth-child(odd) {
        th, td {
            background-color: rgba(#000, 0.025);
        }
    }

    table {
        tr:nth-child(odd) {
            th, td {
                background-color: transparent;
            }
        }
    }
}

table.table.table-hover {
    tr:hover {
        th, td {
            background-color: rgba(#000, 0.05) !important;
        }
    }
    tr.tr-on {
        th[rowspan], td[rowspan] {
            background-color: rgba(#000, 0.05) !important;
        }
    }

    table {
        tr:hover {
            th, td {
                background-color: transparent !important;
            }
        }
    }
}

.table-wrapper {
    overflow-x: auto;
    width: 100%;

    table {
        margin-bottom: 0;
    }
}

.clip-inner-table {
    padding: 0 !important;

    > table {
        border-bottom: none;

        th, td {
            &:first-child {
                border-left: none;
            }

            &:last-child {
                border-right: none;
            }
        }

        tr {
            &:first-child {
                th, td {
                    border-top: none;
                }
            }
        }
    }

    > table.table.table-bordered {
        border: none;

        tr {
            &:first-child {
                th, td {
                    border-top: none;
                }
            }

            &:last-child {
                th, td {
                    border-bottom: none;
                }
            }
        }
    }
}

// Table
table.table {
    @include table-size;
}

// Table sizes
@each $key, $size in $table-sizes {
    $name: class-name($key);

    table.#{$name}table {
        @include table-size($size);
    }
}

// Responce
@include media-breakpoint-down(dt) {
    @include table-responsive("dt");
}


@include media-breakpoint-down(tb) {
    @include table-responsive("tb");
}


@include media-breakpoint-down(sp) {
    @include table-responsive("sp");
}
