//
// Layout
//

.layout-column {
    @include clearfix;

    @include media-breakpoint-up(mx) {
        > .lc-side-left {
            margin-right: $layout-between;
        }

        > .lc-side-right {
            margin-left: $layout-between;
        }
    }
    @include media-breakpoint-up(dt-lg) {
        @include support-flexbox {
            display: flex;

            > .lc-main {
                flex: 1;
                order: 2;
                margin-right: 0;
                padding-right: 0;
            }

            > .lc-side-left {
                flex-basis: $side-left-width;
                order: 1;
                margin-right: $layout-between;
            }

            > .lc-side-right {
                flex-basis: $side-right-width;
                order: 3;
                margin-left: $layout-between;
            }
        }
    }
    @include media-breakpoint-between(tb, dt) {
        .lc-main {
            margin-right: -$side-left-width;
            padding-right: $side-left-width + topx($layout-between);
        }

        .lc-side-left {
            width: $side-left-width;
        }

        .lc-side-right {
            width: $side-left-width;
        }
    }
    @include media-breakpoint-down(tb) {
        .lc-item {
            float: none;
            width: 100%;
            margin-right: 0;
            padding-right: 0;
        }
    }
    @include media-breakpoint-between(sp, tb) {
        @include support-flexbox {
            display: flex;
            flex-wrap: wrap;

            > .lc-main {
                flex: 100%;
                margin-right: 0;
                padding-right: 0;
            }

            > .lc-side-left {
                flex-basis: $side-left-width;
                margin-right: $mlayout-between;
            }

            > .lc-side-right {
                flex: 1;
            }
        }
    }
    .lc-main {
        float: left;
        width: 100%;
        margin-right: -$side-right-width;
        margin-bottom: $layout-between;
        padding-right: $side-right-width + topx($layout-between);
        &:last-child {
            margin-right: 0;
            padding-right: 0;
        }
    }

    .lc-side-left {
        float: right;
        width: $side-right-width;
        margin-bottom: $layout-between;

        @include media-breakpoint-down(tb) {
            margin-bottom: $mlayout-between;
        }
    }

    .lc-side-right {
        float: right;
        clear: right;
        width: $side-right-width;
        margin-bottom: $layout-between;

        @include media-breakpoint-down(tb) {
            margin-bottom: $mlayout-between;
        }
    }
}
