//
// Label
//

@mixin label() {
    font-size: .875em;
    line-height: 1;
    display: inline-block;
    min-width: 4em;
    padding: 0.5em 0.875em;
    text-align: center;
    vertical-align: middle;
    text-indent: 0.1em;
    letter-spacing: 0.1em;
    border: $border-width solid $border-color;
    border-radius: 3px;
    background-color: $white;
}
