//
// Column
//

.column {
    margin-top: $elements-margin-top;
    margin-bottom: $elements-margin-bottom;
    @include support-flexbox {
        > .row {
            display: flex;
            flex-wrap: wrap;
            align-content: stretch;

            .col {
                display: flex;
                flex-direction: column;

                .col-inner {
                    padding: $column-inner-padding;
                    height: 100%;
                }
            }
        }

        &.-rtl {
            > .row {
                flex-direction: row-reverse;
            }
        }
    }


    @include not-support-flexbox {
        @include clearfix;

        > .row {
            .col {
                float: left;
            }
        }
    }
}

