:root {
    @each $color, $value in $colors {
        --#{$color}: #{$value};
    }

    @each $color, $value in $theme-colors {
        --#{$color}: #{$value};
    }

    @each $color, $value in $status-colors {
        --#{$color}: #{$value};
    }

    @each $color, $value in $social-colors {
        --#{$color}: #{$value};
    }
    
    --base-size: #{inspect($base-size)};
    @each $bp, $value in $grid-breakpoints {
        --breakpoint-#{$bp}: #{$value};
    }
    
    --font-family-sans-serif: #{inspect($font-family-sans-serif)};
    --font-family-sans-serif-en: #{inspect($font-family-sans-serif-en)};
    --font-family-serif: #{inspect($font-family-serif)};
    --font-family-serif-en: #{inspect($font-family-serif-en)};
    --font-family-monospace: #{inspect($font-family-monospace)};
}
