//
// Typography
//

// Text align
@mixin text-aligns($device: "") {
    $device: class-name($device);

    .#{$device}text {
        @at-root {
            #{&}-left {
                text-align: left;
            }

            #{&}-center {
                text-align: center;
            }

            #{&}-right {
                text-align: right;
            }

            #{&}-justify {
                text-align: justify;
                text-justify: inter-ideograph;
            }
        }
    }
}

// Text size
@mixin text-sizes($device: "") {
    $device: class-name($device);

    .#{$device}text {
        @at-root {
            #{&}-xx-large {
                font-size: ms(4);
            }

            #{&}-x-large {
                font-size: ms(3);
            }

            #{&}-large {
                font-size: ms(2);
            }

            #{&}-medium {
                font-size: ms(1);
            }

            #{&}-normal {
                font-size: ms(0);
            }

            #{&}-small {
                font-size: ms(0, 0.750em);
            }

            #{&}-x-small {
                font-size: ms(0, 0.5em);
            }

            #{&}-xx-small {
                font-size: ms(0, 0.250em);
            }
        }
    }
}