//
// Loading
//

@keyframes spinner-flickr {
    0% {
        transform: translate(0, 0);
    }

    50% {
        transform: translate($spinner-size / 2, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes spinner-flickr-after {
    0% {
        z-index: 9;
        transform: translate(0, 0);
    }

    49% {
        z-index: 9;
    }

    50% {
        z-index: 11;
        transform: translate($spinner-size / 2, 0);
    }

    99% {
        z-index: 11;
    }

    100% {
        z-index: 9;
        transform: translate(0, 0);
    }
}

.spinner-flickr {
    display: inline-block;
    position: relative;
    width: $spinner-size;
    height: $spinner-size;
    margin: 0 auto;
    transform: translate(-($spinner-size / 2), -($spinner-size / 2)) translate($spinner-size / 2, $spinner-size / 2);

    &:before,
    &:after {
        position: absolute;
        top: $spinner-size / 4;
        left: 0;
        width: $spinner-size / 2;
        height: $spinner-size / 2;
        border-radius: 50%;
    }

    &:before,
    &:after {
        display: block;
        content: "";
    }

    &:before {
        z-index: 10;
        animation: spinner-flickr 1s linear infinite;
        animation-delay: 0s;
        background: $accent-color;
    }

    &:after {
        z-index: 11;
        animation: spinner-flickr-after 1s linear infinite;
        animation-delay: -0.5s;
        background: $theme-color;
    }
}

@keyframes spinner {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.spinner {
    display: inline-block;
    width: $spinner-size;
    height: $spinner-size;
    margin: 0 auto;
    animation: spinner 1s infinite linear;
    border-top: 2px solid $theme-color;
    border-right: 2px solid $theme-color;
    border-bottom: 2px solid $theme-color;
    border-left: 2px dashed transparent;
    border-radius: 50%;
}
