// Document

html {
    line-height-step: $base-line-height-step;
    overflow-y: scroll;
}

// Scrollbar
::selection {
    color: darken($base-color, 7%);
    background: lighten($link-color, 30%);
}

// Headings

h1, h2, h3, h4, h5, h6 {
    margin-top: $headings-margin-top;
}

// Paragraph

p {
    margin-top: $paragraph-margin-top;
}

// Abbreviations

ol,
ul,
dl {
    margin-top: $paragraph-margin-top;
}

// Links

a {
    cursor: pointer;

    &.current {
        cursor: default;
    }

    &:visited {
        color: $link-visited-color;

        @include hover {
            color: $link-visited-hover-color;
        }
    }

    // Bigger link
    &.link {
        body & {
            cursor: pointer;
            color: $body-color !important;
            text-decoration: none;
        }
    }
}

// Code

pre {
    white-space: pre-wrap;
}

// Figures

figure {
    display: inline-block;

    img {
        margin: 0 0 .5rem;
    }

    figcaption {
        font-size: 80%;
        text-align: right;
    }
}

// Images and content

img {
    max-width: 100%;
    height: auto;
}

// Tables

th {
    text-align: left;
}

// Forms

label {
    &:last-child {
        margin-bottom: 0;
    }
}

// Button

button,
html [type="button"],
[type="reset"],
[type="submit"] {
    &:focus {
        outline: 0;
    }

    &:active {
        border-style: solid;
    }
}
