//
// Pre highlight
//

.pre-container {
    overflow: auto;
    clear: both;
    // position: relative;
    font-size: .938em;
    text-align: left;
    letter-spacing: .5px;
    word-wrap: break-word;
    color: $gray;
    background: darken($base-color, 10%);
    border-radius: $border-radius;

    // white-space: nowrap; // 

    pre, code {
        margin: 0;
        padding: 0;
    }

    .table-pre {
        width: 100%;
        background: darken($base-color, 10%);
        // table-layout: fixed;
        border: none;

        th,
        td {
            padding: .125em 1em !important;
            vertical-align: top !important;
            border: none;
        }

        th {
            width: auto;
            position: sticky;
            left: 0;
            text-align: right;
            color: $dark;
            background: darken($base-color, 7%);
            user-select: none;
            -webkit-touch-callout: none;

            &::selection {
                background: none;
            }
        }

        td {
            width: 100%;

            &::selection {
                background: lighten($base-color, 40%);
            }
        }

        .break-row {
            th,
            td {
                padding-top: 2.250em !important;
            }
        }
    }
}

.pre-copy {
    display: flex;
    position: absolute;
    top: .125em;
    right: .375em;
    z-index: 100;
    vertical-align: top;
    text-align: center;
    color: $white !important;
    background: none;
    border: 0;
    border-radius: 50%;

    &:before {
        content: '+';
        display: block;
        position: absolute;
        top: 2px;
        right: 2px;
        color: darken($base-color, 10%);
    }

    &:after {
        content: '+';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
    }

    .no-js & {
        display: none;
    }
}

.pre-onoff {
    display: none;
    width: 100%;
    height: 30%;
    min-height: 2.5em;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    margin: auto;
    padding: 0;
    text-align: center;
    background: linear-gradient(to bottom, rgba($black, 0) 0%, $black 80%);
    border: 0;

    &:before {
        content: '';
        display: inline-block;
        width: 1em;
        height: 1em;
        transform: rotate(135deg);
        border-top: 3px solid $link-color;
        border-right: 3px solid $link-color;
    }

    .pre-container.open & {
        width: 2em;
        height: 2em;
        background: none;

        &:before {
            transform: rotate(315deg);
        }
    }
}

.pre-message {
    width: 80%;
    max-width: 500px;
    height: 7em;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    margin: auto;
    line-height: 7em;
    text-align: center;
    color: $white;
    background: rgba($black, 0.8);
    border-radius: 1em;

    &:after {
        content: '';

        @include ellipsis;
    }

    &.error {
        background: rgba($danger, 0.8);

        &:after {
            content: '';
        }
    }
}
