//
// Table
//

@mixin table-size($size: $table-default-size) {
    th, td {
        padding: $size;
    }
}

@mixin table-responsive($device: "") {
    $device: class-name($device);

    table.table.#{$device}responsive {
        display: block;
        overflow: hidden;

        thead,
        tbody,
        tfoot {
            display: block;
            width: 100%;

            thead {
                display: table-header-group;
                width: auto;
            }

            tbody {
                display: table-row-group;
                width: auto;
            }

            tfoot {
                display: table-footer-group;
                width: auto;
            }
        }

        tr {
            display: block;
            width: 100%;

            tr {
                display: table-row;
                width: auto;
            }
        }

        th, td {
            display: block;
            width: 100%;
        }

        table {
            th, td {
                display: table-cell;
                width: auto;
            }
        }

        &.-fill {
            th, td {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
}