//
// Slidemenu
//

body.init-slidemenu {
    #navi, #content {
        backface-visibility: hidden;
    }

    #navi {
        transition: transform $slidemenu-transition, transform $slidemenu-transition;
    }

    #content {
        opacity: 1;
        transition: transform $slidemenu-transition, opacity $slidemenu-transition;
    }

    @include media-breakpoint-down(tb) {
        #wrapper {
            overflow: hidden;
        }

        #container {
            position: relative;
        }

        #navi {
            width: $slidemenu-width;
            max-width: $screen-sp;
            min-width: 320px;
            height: 100%;
            position: absolute;
            top: 0;
            right: 100%;
            z-index: 10;

            body.open-slidemenu & {
                transform: translateX(100%);
            }
        }

        #content {
            position: relative;

            body.open-slidemenu & {
                transform: translateX(280px);
                opacity: $slidemenu-content-opacity;
            }
        }
    }
}

$hamburger-width: 28px;
$hamburger-height: ceil($hamburger-width / 16 * 9);
$hamburger-bar-height: 2px;
$hamburger-rotate-ratio: $hamburger-width / 4 / 4 + $hamburger-bar-height / 2;

#slideButton {
    display: block;
    width: $hamburger-width;
    height: $hamburger-height;
    position: relative;
    cursor: pointer;
    outline: $border-width solid transparent;
    user-select: none;
    will-change: transform;
    transition: transform $slidemenu-transition;

    @include media-breakpoint-up(slidemenu-breakpoint) {
        display: none;
    }


    tap-highlight-color: rgba(0, 0, 0, 0);
    text-indent: -999em;
    touch-callout: none;

    &:after,
    &:before,
    span {
        content: '';
        display: block;
        width: 100%;
        height: $hamburger-bar-height;
        position: absolute;
        top: 0;
        right: 0;
        background-color: $headings-color;
        outline: $border-width solid transparent;
        will-change: transform;
        transition: transform $slidemenu-transition, width $slidemenu-transition;
        backface-visibility: hidden;
    }

    &:after {
        margin-top: 0;
    }

    span {
        margin-top: 25%;
    }

    &:before {
        margin-top: 50%;
    }
}

@include media-breakpoint-down(slidemenu-breakpoint) {
    #slideButton {
        body.open-slidemenu & {
            transform: rotate(180deg);
        }

        &:after {
            body.open-slidemenu & {
                width: $hamburger-width / 2;
                top: $hamburger-rotate-ratio;
                right: -$hamburger-rotate-ratio;
                transform: rotate(-135deg);
            }
        }

        &:before {
            body.open-slidemenu & {
                width: $hamburger-width / 2;
                top: -$hamburger-rotate-ratio;
                right: -$hamburger-rotate-ratio;
                transform: rotate(135deg);
            }
        }
    }
}
