//
// Form
//

input,
textarea,
select,
label {
    font-size: $font-size-base;
}

input[type="text"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="email"],
input[type="date"],
textarea {
    max-width: 100%;
    padding: $form-element-padding;
    color: inherit;
    background-color: $form-input-bg;
    border: $border-width solid $border-color;
    border-radius: $border-radius;
    box-shadow: inset $box-shadow;
    outline: $border-width solid transparent;

    &::placeholder {
        color: $gray-500;
    }

    &:focus {
        background-color: $form-input-focus-bg;
        border-color: lighten($link-color, 20%) !important;
        border-radius: $border-radius;
        box-shadow: inset $box-shadow, 0 0 5px 2px lighten($link-color, 20%);

        &::placeholder {
            opacity: 0;
        }
    }

    &:first-child {
        margin-top: 0;
    }
}

textarea {
    min-height: 4em;
    vertical-align: top;
    width: $textarea-size;

    @include media-breakpoint-down(sp) {
        max-width: 100%;
    }
}

.input, select {
    width: $input-default-size;
}

@each $key, $size in $input-sizes {
    $name: class-name($key);

    .#{$name}input,
    .#{$name}select {
        width: $size;
    }
}

input[type="checkbox"],
input[type="radio"] {
    margin: 0 0.250em;
    text-align: center;
    cursor: pointer;

    &:first-child {
        margin-left: .250em;
    }
}

select {
    display: inline-block;
    max-width: 100%;
    height: $base-size * 2;
    margin-top: 0.25em;
    border: $border-width solid $border-color;
    border-radius: $border-radius;
    box-shadow: rgba($border-color, 0.3) 1px 1px 1px, rgba($border-color, 0.2) -1px -1px 4px inset;
    cursor: pointer;

    &:first-child {
        margin-top: 0;
    }
}

option {
    cursor: pointer;
}

select[size] {
    overflow: auto;
    cursor: default;
}

label {
    display: inline-block;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
    border: $border-width solid $border-color;
    border-radius: $border-radius;
    box-shadow: rgba($border-color, 0.3) 1px 1px 1px, rgba($border-color, 0.2) -1px -1px 4px inset;
    cursor: pointer;
}

button,
html [type="button"],
[type="reset"],
[type="submit"],
select {
    padding: $form-element-padding;
    line-height: 1;
    vertical-align: top;
    background-color: $gray-100;
    box-shadow: inset 0 -0.5em 0.5em rgba($gray-800, 0.04), $box-shadow;
}

//
// 
//

.select-container {
    display: inline-block;
    height: $base-size * 2;
    position: relative;
    background-color: $gray-100;
    border: $border-width solid $border-color;
    border-radius: $border-radius;
    box-shadow: inset 0 -0.5em 0.5em rgba($gray-800, 0.04), $box-shadow;

    select {
        position: relative;
        z-index: 2;
        background: none;
        border: 0;
        border-radius: 0;
        box-shadow: none;

        &:focus {
            outline: 0;
        }
    }

    @supports (appearance: none) {
        select {
            padding-right: $base-size * 2;
            appearance: none;
        }

        &:after {
            content: "";
            width: $base-size * 1.5;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 1000;
            z-index: 1;
            font-size: 0.625em;
            line-height: $base-size * 2;
            text-align: center;
            color: $gray-600;
            background-color: $gray-200;
            border-left: $border-width solid $border-color;
            border-top-right-radius: $border-radius;
            border-bottom-right-radius: $border-radius;
            box-shadow: inset 0 -0.5em 0.5em rgba($black, 0.04);
        }
    }

    @supports not (appearance: none) {
        &:after {
            display: none !important;
        }
    }
}
