//
// Styles
//

.lead {
    line-height: 2;
}

.ellipsis {
    @include ellipsis;
}

//
// Clear
//

.clearfix {
    @include clearfix;
}

@include clear;

//
// Float
//

@include float;

//
// Space
//

@include space();

//
// Display
//

.flexbox {
    display: flex;
}

.block {
    display: block;
}

.inline-block {
    display: inline-block;
}

.inline {
    display: inline;
}

//
// Position
//

.relative {
    position: relative;
}

.absolute {
    position: absolute;
}

.fixed {
    position: fixed;
}

// Fixed
.fixed-top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: $zindex-fixed;
}

.fixed-bottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $zindex-fixed;
}

// Sticky
.sticky-top {
    @supports (position: sticky) {
        position: sticky;
        top: -1px;
        left: 0;
        width: 100%;
        z-index: $zindex-sticky;
    }
}

.sticky-bottom {
    @supports (position: sticky) {
        position: sticky;
        bottom: -1px;
        left: 0;
        width: 100%;
        z-index: $zindex-sticky;
    }
}

// Zindex
.zindex10 {
    position: relative;
    z-index: 10;
}

.zindex100 {
    position: relative;
    z-index: 100;
}

.zindex1000 {
    position: relative;
    z-index: 1000;
}

.zindex10000 {
    position: relative;
    z-index: 10000;
}

.zindex100000 {
    position: relative;
    z-index: 100000;
}

//
// Sizes
//

@for $i from 1 through 20 {
    .width#{$i * 5} {
        width: unquote("#{5 * $i}% !important");
    }
}

.height100 {
    height: 100%;
}

//
// Color
//

@each $key, $color in $colors {
    .color-#{$key} {
        color: $color !important;
    }

    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $theme-colors {
    .color-#{$key} {
        color: $color !important;
    }

    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $status-colors {
    .color-#{$key} {
        color: $color !important;
    }

    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $social-colors {
    .color-#{$key} {
        color: $color !important;
    }

    .bg-#{$key} {
        background-color: $color !important;
    }
}

//
// Background
//

// Background repeat
.bg-repeat {
    background-repeat: repeat;
}

.bg-no-repeat {
    background-repeat: no-repeat;
}

.bg-repeat-x {
    background-repeat: repeat-x;
}

.bg-repeat-y {
    background-repeat: repeat-y;
}

// Background position
.bg-center-center {
    background-position: center center;
}

.bg-left-center {
    background-position: left center;
}

.bg-right-center {
    background-position: right center;
}

.bg-center-top {
    background-position: center top;
}

.bg-center-bottom {
    background-position: center bottom;
}

.bg-left-top {
    background-position: left top;
}

.bg-right-top {
    background-position: right top;
}

.bg-left-bottom {
    background-position: left bottom;
}

.bg-right-bottom {
    background-position: right bottom;
}

// Background size
.bg-cover {
    background-size: cover;
}

.bg-contain {
    background-size: contain;
}

.bg-height-fit {
    background-size: auto 100%;
}

.bg-width-fit {
    background-size: 100% auto;
}

// Background attachment
.bg-fixed {
    background-attachment: fixed;
}

// Background none
.bg-none {
    background: none;
}

.bg-image-none {
    background-image: none;
}

.bg-color-none {
    background-color: none;
}

// Background color
@each $key, $color in $theme-colors {
    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $theme-colors {
    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $colors {
    .bg-#{$key} {
        background-color: $color !important;
    }
}

@each $key, $color in $grays {
    .bg-#{$key} {
        background-color: $color !important;
    }
}

//
// Code
//
code {
    display: inline-block;
    margin-bottom: .125em;
    padding: 0 .5em;
    font-size: .875em;
    vertical-align: bottom;
    background-color: $white;
    border: $border-width solid $border-color;
    border-radius: $border-radius * 0.75;
}

//
// Pre
//
pre {
    code {
        padding: 0 .5em;
        font-size: 1em;
        background-color: inherit;
        border: 0;
        border-radius: 0;
    }
}

//
// Br
//
.dt {
    @at-root {
        #{&}-up-nobr {
            @include media-breakpoint-up(dt) {
                br {
                    display: none;
                }
            }
        }

        #{&}-down-nobr {
            @include media-breakpoint-down(tb) {
                br {
                    display: none;
                }
            }
        }

        #{&}-nobr {
            @include media-breakpoint-between(tb, dt) {
                br {
                    display: none;
                }
            }
        }
    }
}

.tb {
    @at-root {
        #{&}-up-nobr {
            @include media-breakpoint-up(tb) {
                br {
                    display: none;
                }
            }
        }

        #{&}-down-nobr {
            @include media-breakpoint-down(sp) {
                br {
                    display: none;
                }
            }
        }

        #{&}-nobr {
            @include media-breakpoint-between(sp, tb) {
                br {
                    display: none;
                }
            }
        }
    }
}

.sp {
    @at-root {
        #{&}-up-nobr {
            @include media-breakpoint-up(sp) {
                br {
                    display: none;
                }
            }
        }

        #{&}-nobr {
            @include media-breakpoint-down(sp) {
                br {
                    display: none;
                }
            }
        }
    }
}

//
// Iframe
//

iframe {
    border: 0;
}

.iframe-container {
    max-width: $screen_dt;

    .iframe-wrapper {
        position: relative;
        width: 100%;
        padding: 56.25% 0 0 0;
    }

    &[data-screen-rate="4:3"] {
        .iframe-wrapper {
            padding: 75% 0 0 0;
        }
    }

    .iframe-wrapper {
        iframe {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            margin: 0;
        }
    }
}
