@mixin fukidashi($bg, $color) {
    position: relative;
    display: inline-block;
    max-width: 100%;
    padding: .5em 1em;
    font-size: 1em;
    color: $color;
    background: $bg;

    &:before {
        content: "";
        position: absolute;
        bottom: -.250em;
        left: 1em;
        display: none;
        width: .5em;
        height: .5em;
        margin-left: .125em;
        background: inherit;
        transform: rotate(-45deg);

        @include support-trandform {
            display: block;
        }
    }
}

.fukidashi {
    @include fukidashi($base-color, $white);
}
