//
// View
//

// Full window
.full-window {
    width: 100%;
    height: 100%;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
}

// Centering content
.centering-container {
    display: table;
    width: 100%;

    .centering-wrapper {
        display: table-cell;
        width: 100%;
        height: 100%;
        vertical-align: middle;
        text-align: center;
    }
}

// Y scroll
.unit-y-scoroll {
    position: relative;

    &.unit-init {
        max-height: $base-size * 10;
        overflow-y: hidden;
    }

    &.open {
        max-height: none;
    }

    .no-js & {
        overflow-y: auto;
    }
}
