//
// Typography
//

//
// Headings
//
h1, h2, h3, h4, h5, h6 {
    font-weight: $headings-font-weight;
    font-feature-settings: "palt";
    font-family: $headings-font-family;
    line-height: $headings-line-height;
    color: $headings-color;

    small {
        font-size: .5em;
        opacity: .75;
    }

    a {
        color: $headings-color;
    }
}

// Headings size
h1 {
    font-size: $h1-font-size;
}

h2 {
    font-size: $h2-font-size;
}

h3 {
    font-size: $h3-font-size;
}

h4 {
    font-size: $h4-font-size;
}

h5 {
    font-size: $h5-font-size;
}

h6 {
    font-size: $h6-font-size;
}

@include media-breakpoint-down(tb) {
    h1, h2, h3, h4, h5, h6 {
        line-height: $mheadings-line-height;
    }

    h1 {
        font-size: $mh1-font-size;
    }

    h2 {
        font-size: $mh2-font-size;
    }

    h3 {
        font-size: $mh3-font-size;
    }

    h4 {
        font-size: $mh4-font-size;
    }

    h5 {
        font-size: $mh5-font-size;
    }

    h6 {
        font-size: $mh6-font-size;
    }
}

//
// Horizontal rules
//
hr {
    clear: both;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: $border-width solid $border-color;
    margin-top: $elements-margin-top;
    margin-bottom: $elements-margin-bottom;

    &.hr-dotted {
        border-bottom-style: dotted;
    }

    &.hr-dashed {
        border-bottom-style: dashed;
    }

    &.hr-double {
        height: $border-width * 3;
        border-top: ($border-width * 2) solid $border-color;
        border-bottom-width: $border-width;
    }
}

//
// Emphasis
//
p {
    margin-top: $elements-margin-top;
    margin-bottom: $elements-margin-bottom;
}

ul, ol {
    ul, ol {
        margin-top: 0;
        margin-bottom: 0;
    }
}

dl {
    dt {
        margin-top: $elements-margin-top;
    }

    dd {
        margin-top: $elements-margin-top / 2;
    }
}

mark {
    padding: $mark-padding;
    background: $mark-bg;
    background: linear-gradient(transparent 80%, $mark-bg 80%);
}

// font
.font-palt {
    font-feature-settings: "palt";
}

// Text styles
@include text-aligns;
@include text-sizes;

// styles
.lead {
    line-height: 2;
}