//
// Styles
//

// Sizes
@mixin msizes {
    font-size: $mfont-size-base;
    line-height: $mline-height-base;
}

// Ellipsis
@mixin ellipsis($no-wrap: true) {
    @if $no-wrap {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    @else {
        overflow: visible;
        white-space: nowrap;
        text-overflow: "";
    }
}

//
// Clearfix
//

@mixin clearfix($clear: both) {
    zoom: 1;

    &:after {
        display: block;
        clear: $clear;
        content: "";
    }
}

@mixin clear($add-name: "") {
    $add-name: class-name($add-name);

    .#{$add-name}clear {
        clear: both;
    }

    .#{$add-name}clear-left {
        clear: left;
    }

    .#{$add-name}clear-right {
        clear: right;
    }
}

//
// Float
//

@mixin float($add-name: "") {
    $add-name: class-name($add-name);

    .#{$add-name}align {
        @at-root {
            #{&}left {
                float: left;
                margin-right: 2em;
                margin-bottom: 2em;
            }

            #{&}right {
                float: right;
                margin-bottom: 2em;
                margin-left: 2em;
            }

            #{&}center {
                display: block;
                margin-right: auto;
                margin-left: auto;
            }

            #{&}none {
                float: none;
                margin-right: 0;
                margin-left: 0;
            }
        }
    }

    .#{$add-name}float {
        @at-root {
            #{&}-left {
                float: left !important;
            }

            #{&}-right {
                float: right !important;
            }

            #{&}-none {
                float: none !important;
            }
        }
    }
}

//
// Space
//

@mixin space {
    @each $prop, $abbrev in (margin: m, padding: p) {
        @each $size, $lengths in $spacers {
            $length-x: map-get($lengths, x);
            $length-y: map-get($lengths, y);

            .#{$abbrev}#{$size} {
                #{$prop}: $length-y $length-x !important;
            }

            .#{$abbrev}t#{$size} {
                #{$prop}-top: $length-y !important;
            }

            .#{$abbrev}r#{$size} {
                #{$prop}-right: $length-x !important;
            }

            .#{$abbrev}b#{$size} {
                #{$prop}-bottom: $length-y !important;
            }

            .#{$abbrev}l#{$size} {
                #{$prop}-left: $length-x !important;
            }
        }
    }
}
