//
// Scroll bar
//

body {
    ::-webkit-scrollbar {
        width: $base-size;
        height: $base-size;
    }

    ::-webkit-scrollbar-track {
        // background-color: $light;
        box-shadow: inset 0 0 1px 1px rgba(0, 0, 0, 0.1);
    }

    ::-webkit-scrollbar-thumb {
        border: solid ($base-size / 4 + 1) transparent;
        border-right-width: $base-size / 4;
        border-radius: $base-size;
        box-shadow: inset 0 0 $base-size $base-size rgba(120, 120, 120, 0.5);

        &:horizontal {
            border-color: transparent;
        }
    }
}
