//
// Supports
//

// Flexbox
@mixin support-flexbox {
    @supports (display: flex) {
        @content;
    }

    // IE10
    _:-ms-lang(x), selector {
        @content;
    }
}

@mixin not-support-flexbox {
    @supports not (display: flex) {
        @content;
    }

    // IE9
    @media screen and (min-width: 0\0) and (min-resolution: 0.001dpcm) {
        @content;
    }

    // IE8
    @media \0screen {
        @content;
    }
}

